<template>
  <div class="merchant">
    <app-header show-back="true" title="商户申请" />
    <div class="content">
      <div class="title"><span>填写申请资料</span> <span @click="showModal">已有账号？</span></div>
      <mt-field v-show="!isModify" label="登录名" placeholder="请输入登录名" v-model="merchantInfo.account"></mt-field>
      <mt-field v-show="!isModify" label="密码" placeholder="请输入密码" type="password" v-model="merchantInfo.password"></mt-field>
      <mt-field label="公司名称" placeholder="请输入公司名称" v-model="merchantInfo.merchantName"></mt-field>
      <mt-field label="公司品牌" placeholder="请输入公司品牌" v-model="merchantInfo.brand"></mt-field>
      <mt-field label="店铺名称" placeholder="请输入店铺名称" v-model="merchantInfo.shopName"></mt-field>
      <a href="javascript:void(0)" class="mint-cell mint-field">
        <div class="mint-cell-wrapper">
          <div class="mint-cell-title"><span class="mint-cell-title">省市区</span></div>
          <div class="mint-cell-value">
            <select v-model="merchantInfo.province">
              <option v-for="(item, index) in SHENGSHI" :key="index" :value="item.value">{{item.value}}</option>
            </select>
            <select v-if="merchantInfo.province" v-model="merchantInfo.city">
              <option v-for="(item, index) in SHENGSHI[merchantInfo.provinceIndex].children" :key="index" :value="item.value" >{{item.value}}</option>
            </select>
          </div>
        </div>
      </a>
      <mt-field label="详细地址" placeholder="请输入详细地址" v-model="merchantInfo.address"></mt-field>
      <mt-field label="营业范围" placeholder="请输入营业范围" v-model="merchantInfo.businessScope"></mt-field>
      <mt-field label="纳税人识别号" placeholder="请输入纳税人识别号" v-model="merchantInfo.taxpayerNumber"></mt-field>
      <mt-field label="联系人姓名" placeholder="请输入联系人姓名" v-model="merchantInfo.contactName"></mt-field>
      <mt-field label="联系人电话" placeholder="请输入联系人电话" v-model="merchantInfo.contactTel">
        <mt-button @click="sendSms" size="small" type="primary">发送</mt-button>
      </mt-field>
      <mt-field label="验证码" placeholder="验证码" v-model="merchantInfo.vCode"></mt-field>
      <mt-field label="联系人邮箱" placeholder="请输入联系人邮箱" v-model="merchantInfo.contactEmail"></mt-field>
      <div class="title">门面照片</div>
      <a href="javascript:void(0)" class="mint-cell mint-field" style="padding:10px 15px;">
        <div class="input">
          <i class="fa fa-upload upload-icon" aria-hidden="true"></i>
          <img :src="photo" class="upload-img" alt="">
          <input type="file" ref="resourcePhoto" v-on:change="uploadFile('photo', 'resourcePhoto')">
        </div>
      </a>
      <div class="title">营业执照</div>
      <a href="javascript:void(0)" class="mint-cell mint-field" style="padding: 10px 15px;">
        <div class="input">
          <i class="fa fa-upload upload-icon" aria-hidden="true"></i>
          <img :src="businessLicense" class="upload-img" alt="">
          <input type="file" ref="businessLicense" v-on:change="uploadFile('businessLicense', 'businessLicense')">
        </div>
      </a>
      <div class="title">法人身份证正面</div>
      <a href="javascript:void(0)" class="mint-cell mint-field" style="padding: 10px 15px;">
        <div class="input">
          <i class="fa fa-upload upload-icon" aria-hidden="true"></i>
          <img :src="corporateIdentityCardPositive" class="upload-img" alt="">
          <input type="file" ref="corporateIdentityCardPositive" v-on:change="uploadFile('corporateIdentityCardPositive', 'corporateIdentityCardPositive')">
        </div>
      </a>
      <div class="title">法人身份证反面</div>
      <a href="javascript:void(0)" class="mint-cell mint-field" style="padding: 10px 15px;">
        <div class="input">
          <i class="fa fa-upload upload-icon" aria-hidden="true"></i>
          <img :src="corporateIdentityCardOpposite" class="upload-img" alt="">
          <input type="file" ref="corporateIdentityCardOpposite" v-on:change="uploadFile('corporateIdentityCardOpposite', 'corporateIdentityCardOpposite')">
        </div>
      </a>
      <a href="javascript:void(0)" class="mint-cell mint-field">
        <div class="mint-cell-wrapper">
          <div class="mint-cell-title" style="width: 20px;"><span class="mint-cell-title">
            <input type="checkbox" v-model="xieyi" @change="">
          </span></div>
          <div class="mint-cell-value">
            <a href="javascript:void(0)">《重卡汇商城在线服务协议》</a>
          </div>
        </div>
      </a>
      <div>
        <mt-button :disabled="!xieyi" @click="submit" size="large" type="primary" style="width: 80%; margin: 1rem auto;">提交</mt-button>
      </div>
    </div>
    <mt-popup
      v-model="popupVisible"
      popup-transition="popup-fade">
      <apply-merchant-modal />
    </mt-popup>
  </div>
</template>

<script>
import {Indicator} from 'mint-ui'
import bus from '../../assets/js/bus'
import AppHeader from '../../components/header/header'
import ApplyMerchantModal from './apply-merchant-modal'
import SHENGSHI from '../../../static/json/shengshi'
import {MerchantUrl} from '../../config/urls'
export default {
  data () {
    return {
      xieyi: '',
      businessLicense: '', // 营业执照
      photo: '',  // 门面照片
      corporateIdentityCardPositive: '', // 身份证正面
      corporateIdentityCardOpposite: '', // 身份证反面
      merchantInfo: {
        account: '', // 登录名
        password: '', // 登陆密码
        merchantName: '', // 商户名称
        shopName: '', // 店铺名称
        brand: '',
        province: '', // 省
        provinceIndex: '',
        city: '', // 市
        address: '', // 详细地址
        businessScope: '',  // 营业范围
        taxpayerNumber: '', // 纳税人识别号
        contactName: '', // 联系人姓名
        contactTel: '', // 联系人电话
        contactEmail: '', // 联系人邮箱
        vCode: '' // 验证码
      },
      SHENGSHI: SHENGSHI,
      isModify: false,
      popupVisible: false
    }
  },
  watch: {
    'merchantInfo.province' (val) {
      let S = this.SHENGSHI

      S.forEach((item, index) => {
        if (item.value === val) {
          this.merchantInfo.provinceIndex = index
        }
      })
    }
  },
  mounted () {
    bus.$on('checkAccountSuccess', account => {
      this.isModify = true
      this.photo = account.photo
      this.businessLicense = account.businessLicense
      this.corporateIdentityCardPositive = account.corporateIdentityCardPositive
      this.corporateIdentityCardOpposite = account.corporateIdentityCardOpposite
      for (let i in account) {
        if (this.merchantInfo.hasOwnProperty(i)) {
          this.merchantInfo[i] = account[i]
        }
      }
      this.popupVisible = false
    })
  },
  methods: {
    submit () {
      let info = this.merchantInfo

      this.merchantInfo['businessLicense'] = this.businessLicense
      this.merchantInfo['photo'] = this.photo
      this.merchantInfo['corporateIdentityCardPositive'] = this.corporateIdentityCardPositive
      this.merchantInfo['corporateIdentityCardOpposite'] = this.corporateIdentityCardOpposite

      for (let i in info) {
        if (!info[i]) {
          return this.$toast('请输入完整')
        }
      }

      if (this.isModify) {
        this.merchantInfo['account'] = null
        this.merchantInfo['password'] = null
      }

      this.$http.post(MerchantUrl.submitData, this.merchantInfo)
        .then(data => {
          let datas = data.data
          if (datas.state) {
            this.$toast(datas.errormsg || '信息提交成功，等待审核')
          } else {
            this.$toast(datas.errormsg || '信息提交失败，请重试')
          }
        })
        .catch(err => {
          console.error(err)
          this.$toast('网络错误，请稍后重试!')
        })
    },
    showModal () {
      this.popupVisible = true
    },
    uploadFile (type, el) {
      Indicator.open({
        text: `正在上传`
      })
      let formData = new FormData()
      formData.append(0, this.$refs[el].files[0])
      this.$http.post(MerchantUrl.upload, formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      })
        .then(data => {
          let datas = data.data
          if (datas.state) {
            this[type] = datas.data.url
            Indicator.close()
          } else {
            Indicator.close()
            this.$toast('上传失败')
          }
        })
        .catch(err => {
          console.log(err)
          this.$toast('网络错误')
        })
    },
    sendSms () {
      this.$http.post(MerchantUrl.submitData, {contactTel: this.merchantInfo.contactTel})
        .then(data => {
          let datas = data.data
          if (datas.state) {
            this.$toast('发送成功,请注意查收')
          } else {
            this.$toast(datas.errormsg || '发送失败')
          }
        })
        .catch(err => {
          console.log(err)
          this.$toast('网络错误，请稍后重试!')
        })
    }
  },
  components: {AppHeader, ApplyMerchantModal}
}
</script>

<style lang="less" scoped>
  .merchant{
    padding-top: 40px;
    padding-bottom: 90px;
    .content{
      min-height: 500px;
      background-color: #f0eff5;
      .title{
        display: flex;
        padding-top: .3rem;
        margin-bottom: .3em;
        padding-left: 15px;
        padding-right: 15px;
        color: #999;
        span{
          flex: 1;
        }
      }
      .input {
        position: relative;
        width: 100%;
        height: 200px;
        margin: 0;
        border: 1px solid #d9d9d9;
        input{
          position: absolute;
          z-index: 1;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          opacity: 0;
          -webkit-tap-highlight-color: rgba(0,0,0,0);
        }
        .upload-icon{
          font-size: 3rem;
          font-weight: 100;
          color: #999;
          text-align: center;
          display: block;
          line-height: 200px;
        }
      }
    }
  }
  .upload-img{
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0
  }
</style>



