<template>
  <div class="modal">
    <mt-field label="登录名" placeholder="请输入登录名" v-model="account"></mt-field>
    <mt-field label="密码" placeholder="请输入密码" type="password" v-model="password"></mt-field>
    <mt-button @click="check" size="large" type="primary" style="width: 80%; margin: 1rem auto;">提交</mt-button>
  </div>
</template>

<script>
import bus from '../../assets/js/bus'
import {MerchantUrl} from '../../config/urls'
export default {
  data () {
    return {
      account: '',
      password: ''
    }
  },
  methods: {
    check () {
      if (!this.account || !this.password) return this.$toast('请输入完整')
      this.$http.post(MerchantUrl.info, {
        account: this.account,
        password: this.password
      })
        .then(data => {
          let res = data.data
          if (res.state) {
            bus.$emit('checkAccountSuccess', res.data.info)
          } else {
            this.$toast(res.errormsg || '出错啦')
          }
        })
        .catch(err => {
          console.error(err)
          this.$toast('网络错误!')
        })
    }
  }
}
</script>

<style lang="less" scoped>
  .modal {
    width: 300px;
    padding-top: 10px;
  }
</style>



